import React, { useEffect, useRef, useState } from "react";
import { connect } from "react-redux";
import PriceUnit from "./units/PriceUnit";

// <PriceUnit/>
function PricingAsks({ pricingAsks }) {
  const [SectionTopOffset, setSectionTopOffset] = useState(0);
  const [offset, setOffset] = useState(0);
  const SectionTop = useRef();
  useEffect(() => {
    const onScroll = () => {
      setOffset(window.pageYOffset);
      setTimeout(() => {
        setSectionTopOffset(SectionTop.current.offsetTop);
        // console.log(SectionTop.current.offsetTop, SectionTop);
        if (window.innerWidth < 768) {
          setSectionTopOffset((pre) => pre - 600);
        }
        if (window.innerWidth > 768 && window.innerWidth < 992) {
          setSectionTopOffset((pre) => pre - 500);
        }
        if (window.innerWidth > 992) {
          setSectionTopOffset((pre) => pre - 500);
        }
      }, 0);
    };
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);
  return (
    <section className="pricing-asks">
      <div className="container">
        <div
          ref={SectionTop}
          className={`pricing-asks-top ${
            offset > SectionTopOffset ? "show-section-top" : ""
          }`}
        >
          <h2>{pricingAsks.header}</h2>
          <p>{pricingAsks.paragraph}</p>
        </div>
        <div className="pricing-asks-bottom">
          {pricingAsks.items.map((el, inx) => (
            <PriceUnit
              key={inx + el.id}
              className={`${"unit" + (inx + 1)}`}
              AskNum={inx + 1}
              ask={el.ask}
              answer={el.answer}
              index={inx}
            />
          ))}
        </div>
      </div>
    </section>
  );
}

const mapStateToProps = (state) => {
  return {
    pricingAsks: state.dataText.dataJson.pricingPage.asks,
  };
};

export default connect(mapStateToProps)(PricingAsks);
